/**
 * The entry point of the Chat SDK.
 * @module ChatSDK
 */
import SockJS from 'sockjs-client';
import protobuf from 'protobufjs';
import Long from 'long';
import { proto } from './proto';
import { Connection } from './engineCore/connection';
import { Message } from './message/index';
import { utils, Utils } from './utils';
import { Logger, logger } from './logger';
import { Code } from './status';
protobuf.util.Long = Long;
protobuf.configure();
const root: any = protobuf.Root.fromJSON(proto);

Connection.prototype.root = root;

function _getSock(this: Connection): WebSocket {
	if (this.isHttpDNS) {
		const host = this.socketHost[this.hostIndex];
		if (!host) {
			this.times = 1;
			this.autoReconnectNumTotal = 0;
			this.hostIndex = 0;
			this.onClosed && this.onClosed();
			throw new Error('No connected domain name when get websocket.');
		}

		const domain = host.domain;
		const ip = host.ip;
		let url = '';
		const port = host.port;
		if (domain) {
			url = domain;
		} else {
			url = ip;
		}
		if (port && port !== '80' && port !== '443') {
			url += ':' + port;
		}
		if (url) {
			this.url = '//' + url + '/ws';
		}

		const protocol = this.https ? 'https:' : 'http:';
		this.url = protocol + this.url;
	}
	this.connectionTimer && clearTimeout(this.connectionTimer);
	const socket = new (SockJS as any)(this.url, null, {
		// 设置超时降级时间为30秒
		timeout: 30 * 1000,
	});
	return socket;
}

const newLogger = logger.getLogger('defaultLogger');
newLogger.setConfig({
	useCache: false,
	maxCache: 3 * 1024 * 1024,
	color: '',
	background: '',
});
newLogger.enableAll();
Connection.prototype._getSock = _getSock;
Connection.prototype.getUniqueId = utils.getUniqueId;

export interface EasemobChat {
	connection: typeof Connection;
	message: typeof Message;
	utils: Utils;
	logger: Logger;
	/**@hidden */
	statusCode: typeof Code;
}

const easemobChat: EasemobChat = {
	connection: Connection,
	message: Message,
	utils: utils,
	logger: newLogger,
	statusCode: Code,
};

if (typeof window !== 'undefined') {
	window.WebIM = easemobChat;
}

export default easemobChat;
